import React from 'react';
import { Modal, Row, Col, Image, Button } from "react-bootstrap";
import { Link } from "gatsby";

class City extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false
    };
    this.handleShow = this.handleShow.bind(this);
    this.handleClose = this.handleClose.bind(this);
  }

  handleShow = () => {
    this.setState({ 'show': true });
  }

  handleClose = () => {
    this.setState({ 'show': false });
  }

  render() {
    const { show } = this.state;
    return (<div>
      <Button
          variant="primary"
          className="btn btn-lg btn-filled"
          style={{width:'50%'}}
          onClick={this.handleShow}
      >
          Select Your City
      </Button>
      <Modal size='xl' show={show} onHide={this.handleClose} centered>
        <Modal.Header closeButton className="pb-0 border-bottom-0"/>
        <Modal.Body>
          <div className="select-city text-center">
            <Image
              alt="Allstate Hot Chocolate 15k/5k Logo"
              src="https://s3.amazonaws.com/assets.ramraces.com/public/www.hotchocolate15k.com/assets/hot-chocolate/images/logo-cities.png"
            />
              <h3 className="lobster">Select Your City</h3>
              <hr/>
              <Row>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">St. Louis</Link>
                  </p>
                  <span>11/24/19</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Guadalajara</Link>
                  </p>
                  <span>12/01/19</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Tampa</Link>
                  </p>
                  <span>12/15/19</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/phoenix">Phoenix</Link>
                  </p>
                  <span>12/15/19</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">San Francisco</Link>
                  </p>
                  <span>01/12/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Mexico City</Link>
                  </p>
                  <span>01/12/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Monterrey</Link>
                  </p>
                  <span>01/26/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Houston</Link>
                  </p>
                  <span>02/01/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Atlanta</Link>
                  </p>
                  <span>02/02/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Dallas</Link>
                  </p>
                  <span>02/08/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Nashville</Link>
                  </p>
                  <span>02/15/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Charlotte</Link>
                  </p>
                  <span>02/22/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Seattle</Link>
                  </p>
                  <span>03/01/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">San Diego</Link>
                  </p>
                  <span>03/22/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Louisville</Link>
                  </p>
                  <span>04/04/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Philadelphia</Link>
                  </p>
                  <span>04/04/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Minneapolis</Link>
                  </p>
                  <span>04/11/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Detroit</Link>
                  </p>
                  <span>04/18/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Denver</Link>
                  </p>
                  <span>10/04/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Oklahoma City</Link>
                  </p>
                  <span>10/18/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Chicago</Link>
                  </p>
                  <span>11/08/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Kansas City</Link>
                  </p>
                  <span>11/14/20</span>
								</Col>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Columbus</Link>
                  </p>
                  <span>11/15/20</span>
								</Col>
              </Row>
              <hr/>
              <Row>
                <Col xs={6} sm={4} md={3}>
                  <p className="mb-0">
                    <Link to="/">Indianapolis</Link>
                  </p>
                  <span>Notify Me</span>
                </Col>
              </Row>
          </div>
        </Modal.Body>
      </Modal>
    </div>)
  }
}

export default City
